/* Updated responsive layout */

.App {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 0.9vh 5vw 10vh 5vw;
  gap: 2em;
  font-family: 'Source Code Pro', monospace;
  font-size: 16px; 
}

/* Responsive intro section */
.intro-box {
  max-width: 800px;
  width: 100%;
  text-align: left;
}

/* Responsive projects section */
.project-holder {
  max-width: 800px;
  width: 100%;
}

.project{
  color: black;
  cursor: pointer; 
}

.project:hover p {
  background: linear-gradient(to right, #ff6a00, #ee0979);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.project p {
  color: black;
  margin: 0.5em 0;
  position: relative;
  display: inline-block;
}


.project p::after {
  content: '';
  position: absolute;
  width: 0%;
  height: 1px;
  bottom: 0;
  left: 0;
  background: linear-gradient(to right, #ff6a00, #ee0979);
  transition: width 300ms ease;
}

.project:hover p::after {
  width: 100%;
}

/* Mobile adjustments */
@media (max-width: 768px) {
  .App {
    padding: 10vh 8vw 5vh 8vw;
  }

  .intro-box {
    font-size: 14px;
  }

  .project-holder {
    font-size: 13px;
  }
}
